
@import "variables.css";

legend {
  font: 400 1.8rem/1.2 var(--baseHeaderFontFamily);
  display: block;
  border: 0;
  padding: 0 0 0.2rem 0;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 0 2.5rem;
  padding: 1rem 1rem 0;
}


input, textarea, select {
  height: 3.8rem;
  padding: 0.6rem 1rem;
  background-color: #fff;
  border: 1px solid #d1d1d1;
  border-radius: 2px;
  box-shadow: none;
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 2rem;
  &:focus {
     border-color: #aaa;
  }
}

textarea {
  height: 13rem;
}

button, .button {
  box-sizing: border-box;
  color: #fff;
  background-color: #1eaedb;
  border-color: #1eaedb;
  border: 0;
  height: 3.8rem;
  padding: 0 3rem;
  text-transform: uppercase;
  width: 100%;
  font-weight: 600;
  font-size: 1.1rem;
}

.button {
  display: inline-block;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  color: #fff;
  padding-top:1.1rem;
  padding-bottom:1.1rem;
  margin-bottom:1rem;
}
.editor-text a.button {
  color: #fff!important;
}

.form {
  padding: 2.5rem 2rem;
  margin: 0;
}

.form label {
  display: block;
  line-height: 1.2;
  font-weight: 400;
  margin-bottom: 0.2rem;
}

.form label.label-inline {
  display: inline-block;
}
.form label.label-inline:not(:first-child) {
  margin-left:1rem;
}

.cols {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-content: center;
  align-items: center;
}
.cols .col-1,
.cols .col-2 {
  order: 0;
  flex: 0 0 49%;
  align-self: auto;
  margin-bottom: 2rem;
}
.cols {
  & input, & textarea, & select {
     margin-bottom: 0;
   }
}

@media (--timeline-phone) {
  .form {
    padding: 1.5rem 1rem;
  }
  .cols {
    display: block;
  }
  .cols .col-1,
  .cols .col-2 {
    display: block;
  }
  .cols label.col-1,
  .cols label.col-2 {
    margin-bottom: .5rem;
  }
}


.form_errors {
  color: #DA3A40;
  margin: 0;
  padding:  0 0 1rem;
  list-style-type: none;
}

.form_errors li {
  margin:0;
  padding: 0 0 1rem;
  line-height:1.2;
}

input, select, textarea {
  &.has-error {
    box-shadow: 0 1px 0 rgba(255, 119, 119, 0.7) !important;
    /*border-bottom-color: #da545f !important*/
  }
}

input[type="radio"] {
  width: auto;
  height:auto;
  margin-left: 2px;
}

.col-full input {
  margin-bottom: 1.5rem;
}


input[disabled], .input[readonly] {
  background-color: #eee;
  opacity: .9;
  color: #777;
}



