:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.event-box--info {
    padding: 0 .5rem;
    position: relative;
    margin-bottom:.5rem;
}

.event-box--info--title {
    color: #000;
    font-size: 1.2rem;
    line-height:1.2;
}

/* z-index for hover overlay*/

.event-item .event-box--info--title {
    /*position: relative;*/
    /*z-index:25;*/
}

/*
  .event-box--info--overlay {
    position: absolute;
    top:100%;
    left:0;
    margin-top:1px;
    background:#fff;
    width: 16rem;
    box-shadow: 0 0 5px rgba(0,0,0,.5);
    padding: 0 .5rem .2rem;
    box-sizing: border-box;
    display: none;
  }

  .event-box--info--overlay:before {
    position: absolute;
    top:-5px;
    left:0;
    background: #fff;
    width: 16rem;
    height:5px;
    content: '';
    display: none;
  }
*/

.event-item__hover .event-box--info--overlay,
  .event-item__hover .event-box--info--overlay:before {
    display: block;
}

.event-box--info--date {
    color: #222;
    font-size: 1.1rem;
    font-weight: bold;
    z-index: 22;
}

.event-box--info--overlay i {
    width: 1.9rem;
    height: 1.9rem;
    line-height: 1.9rem;
}

.event-box--info--overlay--body {
    font-size: 1.1rem;
    color: #000;
    max-height: 5.2rem;
    overflow: hidden;
}

.event-box--info--overlay--body h2,
  .event-box--info--overlay--body h3,
  .event-box--info--overlay--body p,
  .event-box--info--overlay--body ul,
  .event-box--info--overlay--body li {
    display:inline;
    margin-right: .5em;
    font-size:1em;
    font-weight: normal
}

.event-box--info--overlay--body br {
    display: none;
}

.compact-mode .event-box--info--title {
    line-height: 1.1;
    font-size: 1.1rem;
    font-weight: 500;
    color: #16407d;
    word-wrap: break-word;
}

