:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.calendar-wrapper {
  text-align: center;
  border: none;
  margin: 0;
  overflow: hidden;
}

.calendar {
  text-align: center;
  overflow: hidden;
  padding: 0;
  background: #fff;
}

@media (max-width: 800px) {
.calendar-wrapper {
    margin: 2rem auto 0;
}
.calendar {
    border: 0;
    width: 100%;
    padding: 0 0 .5rem;
}
}



