:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.event-item {
  margin: 0 0 1rem 1.5rem;
  width: 16rem;
  text-decoration: none;
  position: relative;
}

.event-item__hover {
  background: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, .5);
}

.event-box {
  display: table;
  table-layout: fixed;
  height: 4.2rem;
  width: 16rem;
  margin-bottom: .5rem;
  cursor: pointer;
}

.event-box.lowercase span {
  text-transform: none;
  font-weight: 500
}

.event-box.blue-red .event-box--img {
  background: #16407d;
  border: none;
}

.event-box.blue-red .event-box--type {
  background: #e73e42;
  color: #fff;
  border: none;
}

.event-box--img {
  display: table-cell;
  width: 4rem;
  height: 4rem;
  vertical-align: middle;
  background: #9b9b9b;
  z-index: 1;
  border: none;
}

.event-box--img img {
  width: 3.8rem;
  height: 3.8rem;
  margin: .1rem auto;
  padding: 0;
  display: block;
}

.event-box--type {
  display: table-cell;
  vertical-align: middle;
  text-transform: uppercase;
  color: #16407d;
  font-size: 1.1rem;
  font-weight: bold;
  border: 2px solid #9b9b9b;
  border-left: 0;
}

.event-box--type > h3 {
  font-size: 1em;
  line-height: 1;
  padding: 0 .7rem;
  display: block
}

.compact-mode .event-item {
  margin-bottom: 2rem;
  width: 6rem;
  display: inline-block;
  vertical-align: top;
}

.compact-mode .event-item .event-box {
  width: 6rem;
}

.compact-mode .event-box--type {
  display: none !important;
}

/* Vertical layout for timeline */

@media (min-width: 801px) {
.event-item {
    -webkit-box-flex: 0;
        -ms-flex: 0 1 auto;
            flex: 0 1 auto;
    -ms-flex-item-align: auto;
        -ms-grid-row-align: auto;
        align-self: auto;
    -webkit-box-ordinal-group: 1;
        -ms-flex-order: 0;
            order: 0;
}
}

@media (max-width: 800px) {
.event-item {
    margin: 0 2rem 2rem 0rem;
    display: inline-block;
    vertical-align: top;
}
}

@media (max-width: 800px) {
.compact-mode .event-item:last-child {
    margin-bottom: 4rem;
}
.event-item,
  .event-box {
    width: 14.5rem;
}
.event-item {
    margin: 0 1rem 2rem 0;
}
}



