:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.header {
  background-color: #16407d;
  box-sizing: border-box;
}

.ios .header {
  padding-top: 2rem;
  height: 6rem;
}

.ios header.header__menu-on {
  height: auto;
}

.header-wrapper {
  height: 100vh;
  padding: .5rem;
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
  -webkit-box-align: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
}

.header-wrapper > * {
  -webkit-box-ordinal-group: 1;
      -ms-flex-order: 0;
          order: 0;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  -ms-flex-item-align: auto;
      -ms-grid-row-align: auto;
      align-self: auto;
}

.header-wrapper > *:nth-child(4) {
  -webkit-box-ordinal-group: 1;
      -ms-flex-order: 0;
          order: 0;
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  -ms-flex-item-align: auto;
      -ms-grid-row-align: auto;
      align-self: auto;
}

.header_menu-toggle {
  position: fixed;
  overflow-y: auto;
  right: 0rem;
  width: 4.2rem;
  height: 4.2rem;
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  line-height: 4rem;
  text-align: center;
  font-size: 2.4rem;
  color: #E9BE29;
  cursor: pointer;
  display: none;
}

.header_logo {
  background-image: url(../assets/img/ncf-logo-title.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  display: block;
  text-align: center;
  padding: 0;
  height: 11rem;
  width: 100%;
  margin: 2rem auto 2.5rem;
}

@media (min-width: 801px) {
.header {
    position: fixed;
    width: 20rem;
    height: 100%;
}
}

@media (max-width: 800px) {
.header {
    height: 4rem;
    display: block;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
}
.header-wrapper {
    display: block;
    width: 300px;
    height: auto;
    margin-left: auto;
    margin-right: auto;
}
.header-wrapper > * {
    display: none;
}
.header-wrapper > *:nth-child(1) {
    display: block;
    position: relative;
    z-index: 1000;
}
.header_logo {
    margin: 0 auto;
    background-position: 50% 0;
    background-size: 14.25rem 10rem;
    width: 5rem;
    height: 6rem;
}
.header__menu-on {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    height: 100vh;
    overflow-y: auto;
}
.header__menu-on .header_menu-toggle {
    position: fixed;
    top: 0px;
    right: 0px;
}
.header__menu-on .header-wrapper {
    display: block;
    height: auto;
}
.header__menu-on .header-wrapper > * {
    display: block;
    padding-bottom: 2rem;
}
.header__menu-on .header-wrapper .header_logo {
    width: 100%;
    margin-top: 2rem;
    margin-bottom: 1rem;
    background-size: contain;
    height: 12rem;
}
.header_menu-toggle {
    display: inline-block;
}
.header__menu-on .header_menu-toggle i:before {
    content: '\e802';
}
}

@media (max-height: 700px) and (min-width: 801px) {
.header {
    overflow-y: auto;
}
.header-wrapper {
    height: auto;
}
}



