:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.calendar_dropdown {
  position: relative;
  font-size: 1.7rem;
  margin-bottom: 0;
  margin-top: .5rem;
}

.calendar_dropdown span {
  font-weight: normal;
}

.dropdown_title {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  text-align: center;
  pointer-events: none;
}

.dropdown_link {
  border-bottom: 1px dashed #16407d;
  color: #16407d;
}

.dropdown_select {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  line-height: 1;
  height: auto;
  opacity: 0;
}



