:root {
  --blueColor: #16407d;
  --headerBgColor: var(--blueColor);
  --headerHeightLg: 7rem;
  --headerHeightMd: 7rem;
  --headerHeightSm: 4rem;
  --footerHeightSm: 4.2rem;
  --timelineTopPadding: 7rem;
  --baseHeaderFontFamily: "HelveticaNeueCondensed", "HelveticaNeue-Condensed", "Helvetica Neue Condensed", "HelveticaNeueRomanCondensed", "HelveticaNeue-Roman-Condensed", "Helvetica Neue Roman Condensed", "Roboto Condensed", "Open Sans Condensed", "Abadi MT Condensed Light", sans-serif;

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
  --baseFontFamily: -apple-system, BlinkMacSystemFont, “Segoe UI”, Roboto, Helvetica, Arial, sans-serif;
}

@custom-media --header-large (width >= 1180px);
@custom-media --header-medium (width > 800px) and (width < 1180px);
@custom-media --header-small (width <= 800px);

@custom-media --timeline-large (width > 800px);
@custom-media --timeline-small (width <= 800px);
@custom-media --timeline-phone (width <= 480px);

@custom-media --header-overflow (height <= 700px) and (width > 800px);





