
.user-profile {
  border-bottom:1px dotted #ccc;
  margin-bottom:2rem;
  padding-bottom:1rem;
}
.user-profile_avatar {
  float: left;
  display: block;
  width: 5rem;
  height: 5rem;
  line-height:4.7rem;
  font-size: 3rem;
  text-align: center;
  padding: 0;
  border: 1px solid #ccc;
  border-radius: 50%;
  margin-right: 1.5rem;
  color:#999;
  background: #f1f1f1;
}
.user-profile_name {
  margin: 0;
  padding-top: 0.2rem;
}
.user-profile_email {
  color: #999;
  font-size:1.3rem;
  margin-bottom:2rem;
}
.edit-user-link {
  color: #2d67bb;
  font-size: 1.3rem;
  cursor: pointer;
  text-decoration: underline;
}
.edit-user-link:hover {
  text-decoration: none;
}
.login.profile .form {
  padding-bottom: 2rem;
}



