:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.calendar-settings {
  height: 8rem;
}

.calendar-settings_list {
  overflow: hidden;
  display: none;
}

#settingsToggle:checked + .calendar-settings_list {
  display: block;
}

#settingsToggle + .calendar-settings_list + span {
  color: #fff;
  font-size: 1.2rem;
  margin-left: .2rem;
  border-bottom: 1px dashed #fff;
  cursor: pointer;
}

#settingsToggle:checked + .calendar-settings_list + span {
  opacity: 0.4;
}

@media (max-width: 800px) {
.calendar-settings {
    width: 31rem;
    margin: 0 auto;
}
}



