:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.timeline_time-label {
  padding:0;
  width:1px;
  position: relative;
  cursor: pointer;
}

.timeline_time-label:before {
  background: #fff;
  content: '';
  height: 11px;
  width: 11px;
  border: 2px solid #4a90e2;
  display: block;
  border-radius: 50%;
  float: left;
}

.timeline_time-label h3 {
  color: #5e6066;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1rem;
  margin: 0;
  padding: .1rem 0 .5rem 1.8rem;
  white-space: nowrap;
}

.timeline_time-label h4 {
  font-size: 1.3rem;
  font-weight: normal;
  line-height: 1rem;
  padding: .6rem 0 0 1.8rem;
  margin: 0;
  white-space: nowrap;
}

.timeline_time-label__past:before {
  border-color: #62c6f1;
}

.timeline_time-label__past h4 {
  color: #5E6066;
}

.timeline_time-label__current:before {
  background: #e73e42;
  border-color: #fff;
}

@media (min-width: 801px) {
.timeline_time-label {
    margin-top:-7rem;
    padding-top: 1.5rem;
    height:100vh;
}
.timeline_time-label:before {
    margin-top:9px;
}
.timeline_time-label:after {
    top:2.65rem;
}
}

@media (max-width: 800px) {
.timeline_time-label {
    height:auto;
    width:100%;
    margin: 1rem 0 1.5rem -2.7rem;
    padding: 0 !important;
}
.timeline_time-label h3 {
    padding-left:2.7rem;
    padding-bottom:0;
}
.timeline_time-label h4 {
    padding-top:0.4rem;
    padding-left:2.7rem;
    font-weight: bold;
}
.timeline_time-label:before {
    margin-top:7px;
    margin-left:-1px;
}
}



