
.social-buttons {
  font-size:0;
}
.social-buttons i {
  color: #fff;
  background: #16407d;
  font-size: 1.3rem;
  width: 2em;
  height: 2em;
  line-height: 2em;
  text-align: center;
  cursor: pointer;
  display: inline-block;
  border: 0;
  padding: 0;
}
.social-buttons i + i {
  margin-left:.3rem;
}
.social-buttons i:hover {
  background: #ed4044;
}



