:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.timeline_period {
  margin: 0;
  padding: 0;
  width: 20px;
  transition: width 0.3s ease;
}

.timeline_period__empty .timeline_period--notification {
  text-align: center;
  margin-top: 24px;
  font-family: "HelveticaNeueCondensed", "HelveticaNeue-Condensed", "Helvetica Neue Condensed", "HelveticaNeueRomanCondensed", "HelveticaNeue-Roman-Condensed", "Helvetica Neue Roman Condensed", "Roboto Condensed", "Open Sans Condensed", "Abadi MT Condensed Light", sans-serif;
  color: #999;
}

@media (min-width: 801px) {
.timeline_period {
    margin-top: -7rem;
    height:100vh;
}
.timeline_period__empty {
    width: 100%;
}
.timeline_period__limit-left {
    width: 20px;
    /*background: linear-gradient(to right, rgba(0,0,0,.25) 0%, rgba(0,0,0,.07) 20%, rgba(0,0,0,0) 50%);*/
}
.timeline_period__limit-right {
    width: 20px;
    /*background: linear-gradient(to left, rgba(0,0,0,.25) 0%, rgba(0,0,0,.07) 20%, rgba(0,0,0,0) 50%);*/
}
}

@media (max-width: 800px) {
.timeline_period {
    height:auto;
    width:100%;
    margin: 1rem 0 1rem -2.7rem;
    padding: 0 !important;
}
}



