
.calendar_day {
  position: relative;
  border: 1px solid transparent;
}
.calendar_day > a,
.calendar_day > span {
  text-decoration: none;
  width: 100%;
  text-align: center;
  display: inline-block;
  line-height: 1;
  box-sizing: border-box;
  color: #666;
  font-size: 11px;
  padding: .6rem .4rem;
}
.calendar_day > a {
   color: #16407d;
}
.calendar_day.prevmonth,
.calendar_day.nextmonth {
}
.calendar_day.prevmonth > a, .calendar_day.prevmonth > span, .calendar_day.nextmonth > a, .calendar_day.nextmonth > span {
  color: #ddd;
}
.calendar_day.currentday { /* Today */
}
.calendar_day.currentday > a, .calendar_day.currentday > span {
  color: blue;
}
.calendar_day.activeday {
  border-color:#e73e42 !important;
}
.calendar_day.event span:after {
  background: #16407d;
  border-radius: 50%;
  bottom: .1rem;
  display: block;
  content: '';
  width: .5rem;
  height: .5rem;
  left: 50%;
  top:50%;
  margin: .5em 0 0 -.2rem;
  position: absolute;
}
.calendar_day.event:hover {
  border: 1px solid #16407d
}
.calendar_day.event.prevmonth:hover, .calendar_day.event.nextmonth:hover {
  border: 1px solid transparent
}
.calendar_day.event.nextmonth:after,
.calendar_day.event.prevmonth:after{
  background:  #ddd;
}
.calendar_day.event.currentday:after {
  background: blue
}




