}

footer {
  display: block;
  box-sizing: border-box;
  text-align: left;
  z-index: 999;
  margin-top: 2rem;
}

footer > section {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}

footer > section > a {
  display: inline-block;
  -webkit-box-ordinal-group: 1;
      -ms-flex-order: 0;
          order: 0;
  -webkit-box-flex: 0;
      -ms-flex: 0 1 auto;
          flex: 0 1 auto;
  -ms-flex-item-align: auto;
      -ms-grid-row-align: auto;
      align-self: auto;
}

footer .icon {
  display: inline-block;
  color: #16407d;
  background: #fff;
  z-index: 905;
  border: 1px solid #16407d;
  font-size: 1.5rem;
  padding: .2rem;
  cursor: pointer;
}

@media (max-width: 800px) {
footer .icon {
    font-size:2rem;
}
}



