:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.current-date {
  display: block;
  color: #e73e42;
  font-size: 1rem;
  line-height:1rem;
  white-space: nowrap;
  padding:.1rem .2rem;
}

.current-date:before {
  background: #e73e42;
  content: '';
  height: .9rem;
  width: .9rem;
  border: none;
  box-shadow: 0 0 1px #fff;
  display: block;
  border-radius: 50%;
  position: relative;
  z-index:1;
}

.current-date i {
  font-style: normal;
  background: rgba(255, 255, 255, .9);
  padding:0 .2rem;
  margin:0 0 0 -.2rem;
}

@media (min-width: 801px) {
.current-date {
    margin-top: 2.6rem;
    margin-left: -2rem;
}
.timeline_period__empty .current-date {
    margin-left: 50%;
}
}

@media (max-width: 800px) {
.current-date:before {
    display: inline-block;
    margin-right: .7rem;
}
}



