:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.modal-mask {
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
}

.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}

.modal-container {
    width: 100%;
    margin: 0 auto;
    background-color: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: -apple-system, BlinkMacSystemFont, “Segoe UI”, Roboto, Helvetica, Arial, sans-serif;
    position: relative;
    text-align: left;
}

.modal_content {
    height:calc(100vh - 7rem);
}

.modal_content__scroller {
    overflow-y: scroll;
}

@media (min-width: 801px) {
.modal-container {
      max-width: 75rem;
}
.modal_content {
      height: 49rem;
}
}

.modal_header  {
    color: #fff;
    padding: 3rem 2.5rem 1rem;
    background: #ed4044;
    box-sizing: border-box;
    height: 7rem;
}

/*.ios .modal_header {*/

/*height:9rem;*/

/*}*/

.modal_header h3 {
    color: #fff;
    padding: 0;
    margin: 0;
    font: 400 2.4rem/1.2 "HelveticaNeueCondensed", "HelveticaNeue-Condensed", "Helvetica Neue Condensed", "HelveticaNeueRomanCondensed", "HelveticaNeue-Roman-Condensed", "Helvetica Neue Roman Condensed", "Roboto Condensed", "Open Sans Condensed", "Abadi MT Condensed Light", sans-serif;
    text-transform: uppercase
}

.modal_button__close {
    position: absolute;
    z-index: 10000;
    top: 1px;
    right: 1px;
    line-height: 2.5rem;
    width: 2.5rem;
    cursor: pointer;
    text-align: center;
    font-family: Arial, sans-serif;
    background: #fff;
    background: rgba(255, 255, 255, .3);
    color: #000;
}

.ios .modal_button__close {
  top:2rem;
}

.ios .login .modal_button__close {
  top:0;
}

@media (max-width: 800px) {
    /*.event .modal-container {*/
    /*padding-top: 20px;*/
    /*}*/
    /*.event .modal_content {*/
    /*height:calc(100vh - 7rem - 20px);*/
    /*}*/
    /*.event .modal_button__close,*/
    /*.register .modal_button__close,*/
    /*.event-form .modal_button__close {*/
    /*top:20px !important;*/
    /*}*/
.modal_content {
      height:calc(100vh - 9rem);
}
.ios .modal_content {
      height:calc(100vh - 8rem);
}
}




