
.calendar_header {
  position: relative;
}
.calendar_header button {
  text-decoration: none;
  height: 2em;
  font-size: 1em;
  line-height: 2em;
  margin: -1em 0 0 0;
  padding: 0 1rem;
  position: absolute;
  top: 54%;
  width: 2em;
  color: #16407d;
  background: none;
  transition: color .1s linear;
}
.calendar_header button[disabled] {
  color: #ddd;
}
.calendar_header button:not([disabled]):hover {
  color: #000;
}
.calendar_header .btn-prev {
  left: -5px;
}
.calendar_header .btn-next {
  right: 5px;
}



