:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

#toggleCalendar:checked {
  display: none;
}

#toggleCalendar:checked + label + .calendar-view {
  display: block;
}

.filter-toggle-checkbox {
  display: none;
}

#toggleCalendar:checked + label > i.icon-calendar:before {
  content: '\E802';
}

#toggleCalendar:checked + label > .icon.icon-calendar {
  border-color: transparent;
  background: transparent;
}

.calendar-view {
  width:100%;
  height: 100%;
  background: #fff;
  top:0;
  display: none;
  z-index: 904;
  position: absolute;
}

.icon-calendar {
  position: relative;
  z-index:900;
}

@media (max-width: 800px) {
#toggleCalendar:checked + label > .icon.icon-calendar {
    position: absolute;
    top:0;
    right:0;
    left: auto;
}
}

@media (min-width: 801px) {
.calendar-view {
    top:1rem;
    height:auto;
}
#toggleCalendar:checked + label > .icon.icon-calendar {
    left:31rem;
}
}

@media (min-width: 1180px) {
.toggle-calendar {
     display: none;
}
}

@media (min-width: 801px) and (max-width: 1179px) {
.toggle-calendar {
     display: none;
}
}




