:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.filter-list {
  margin: .3rem 0;
  font-size: 0;
  box-sizing: border-box;
  font-family: "HelveticaNeueCondensed", "HelveticaNeue-Condensed", "Helvetica Neue Condensed", "HelveticaNeueRomanCondensed", "HelveticaNeue-Roman-Condensed", "Helvetica Neue Roman Condensed", "Roboto Condensed", "Open Sans Condensed", "Abadi MT Condensed Light", sans-serif;
  float: none;
}

.filter-list button {
  padding: .3rem 0;
  height: auto;
  letter-spacing: 0.5px;
  font-size: 12px;
}

.filter-for-events {
  margin-bottom: 20px;
}



