:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.timeline {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  box-sizing: border-box;
  height: 100vh;
  overflow: hidden;
  overflow-x: scroll;
  margin: 0 0 0 20rem;
  padding: 7rem 0 0 0rem;
  background: linear-gradient(to bottom, #4a90e2 0%, #4a90e2 100%);
  background-size: 3px 3px;
  background-repeat: repeat-x;
  background-position: 0 3rem;
}

.timeline__loading {
  overflow-x: hidden;
  opacity: .5;
}

.timeline__loading * {
  pointer-events: none !important;
}

@media (max-width: 800px) {
.timeline {
    height: calc(100vh - 4rem);
}
}

/* Vertical layout for timeline */

@media (max-width: 800px) {
.timeline {
    display: block;
    padding: 2rem 0 2em 4.2rem;
    margin-left: 0;
    overflow-x: hidden;
    overflow-y: auto;
    background-repeat: repeat-y;
    background-position: 2rem 0;
    width: 100%;
}
.timeline {
    margin-left: 0;
    margin-top: 4rem;
    padding-top: 1rem;
}
}



