:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.footer-button_print,
.footer-button_edit,
.footer-button_delete {
  background: #ed4044;
  border: 1px solid #fff;
  display: block;
  height: auto;
  margin: 0 0 0.8rem;
  padding: 0.3rem 1rem 0.4rem;
  width: 100%;
  text-align: left;
  color: #fff;
  font: 700 1.6rem/1.2 "HelveticaNeueCondensed", "HelveticaNeue-Condensed", "Helvetica Neue Condensed", "HelveticaNeueRomanCondensed", "HelveticaNeue-Roman-Condensed", "Helvetica Neue Roman Condensed", "Roboto Condensed", "Open Sans Condensed", "Abadi MT Condensed Light", sans-serif;
  text-transform: uppercase
}

.footer-button_print
i, .footer-button_edit
i, .footer-button_delete
i {
  float: right
}

.footer-button_print:hover {
  color: #000;
}

.footer-button_edit {
  color: #FFF;
  background-color: #33C3F0;
  border-color: #33C3F0;
}

.footer-button_edit:hover {
  background-color: #1EAEDB;
  border-color: #1EAEDB;
}

.footer-button_delete {
  background-color: #fefefe;
  background: none;
  border-color: #666;
  color: #666;
}

.footer-button_delete:hover {
  border-color: #000;
  color: #000;
}

@media (max-width: 480px) {
.footer-button_print, .footer-button_edit, .footer-button_delete {
    font-size: 1.3rem;
    padding: 1rem .5rem 1rem 1rem;
}
}




