:root {

  /* NOTE: don’t use -apple-system at the head of a shorthand font declaration */
  /*--baseFontFamily: Helvetica Neue, Helvetica, Arial, sans-serif;*/
}

.modal-2cols {
  display: table;
  table-layout: fixed;
  width:100%;
}

.modal-2cols_row {
  display: table-row;
}

.modal-2cols_left {
  display: table-cell;
  width: 22rem;
  vertical-align: top;
}

.modal-2cols_right {
  display: table-cell;
  width: auto;
  vertical-align: top;
}

.event .modal_header {
  padding:0;
}

.event .modal_content {
  position: relative;
}

.event-header {
}

.event-header_image {
  background-color: #989fa3;
  white-space: normal;
  position: relative;
}

.event-header_image img {
  height: 9rem;
  position: absolute;
  z-index: 1;
  top: 1rem;
  left: 1rem;
}

.event-header_title {
  background-color: #16407d;
}

.event-header_title h1 {
  text-transform: uppercase;
  padding: 1.5rem 2.5rem 0;
  font: 400 2.2rem/1.2 "HelveticaNeueCondensed", "HelveticaNeue-Condensed", "Helvetica Neue Condensed", "HelveticaNeueRomanCondensed", "HelveticaNeue-Roman-Condensed", "Helvetica Neue Roman Condensed", "Roboto Condensed", "Open Sans Condensed", "Abadi MT Condensed Light", sans-serif;
  min-height:1.15em;
}

.event-header.blue-red .event-header_image {
  background: #16407D;
}

.event-header.blue-red .event-header_title {
  background: #e73e42;
}

.event-header.grey-black .event-header_title {
  background: #000;
}

.event-header.grey-blue .event-header_title {
  background: #16407D;
}

.event-sidebar {
  padding: 3rem 2.5rem 0 1.5rem;
}

.event-sidebar p {
  font-size: 1.2rem;
  line-height: 1.1;
  margin: 0 0 1em;
}

.tab-switcher {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font: 400 1.3rem/1.2 "HelveticaNeueCondensed", "HelveticaNeue-Condensed", "Helvetica Neue Condensed", "HelveticaNeueRomanCondensed", "HelveticaNeue-Roman-Condensed", "Helvetica Neue Roman Condensed", "Roboto Condensed", "Open Sans Condensed", "Abadi MT Condensed Light", sans-serif;
}

.tab-switcher li {
  margin: 0;
  padding: 0.8em 0 0 2rem;
  position: relative;
  cursor: pointer;
}

.tab-switcher li:before {
  display: block;
  position: absolute;
  left: 0;
  content: '';
  top: 1.1em;
  width: 0.8rem;
  height: 0.8rem;
  background: #16407d;
}

.tab-switcher li.active:before {
  background: #e24343;
}

.tab-switcher li.active:after {
  display: block;
  position: absolute;
  right: 0;
  top: .7em;
  background: url("../../assets/img/delimiter-active.png") no-repeat;
  width: 1.8rem;
  height: 1.8rem;
  margin-right: -33px;
  z-index: 10;
  content: '';
}

.event-content {
  padding: 0 2rem;
}

.event-sidebar:after {
  position: absolute;
  top: 0;
  margin-right: -.6rem;
  right:0;
  bottom: 0;
  width: 0.7rem;
  content: '';
  background: linear-gradient(to right, rgba(0, 0, 0, .1) 0, rgba(46, 46, 46, .25) 18%, rgba(255, 255, 255, 0) 100%);
}

.event-footer {
  opacity: 1;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 22rem;
  padding: 1.5rem;
}

@media (max-width: 480px) {
.tab-switcher {
    font-size:1.6rem;
}
.tab-switcher li {
    padding:1rem 0;
}
.tab-switcher li:before {
    display: none;
}
.event-sidebar {
    padding-left:1rem;
}
.modal-2cols_left {
    width:14rem;
}
.event-footer {
    width:14rem;
    padding:1rem;
}
h1.sub-section {
    max-width: 150px;
}
h1.sub-section > div {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
}

.ios .modal-2cols_left,
.ios .modal_header {
  height: 8rem;
}

.ios .login .modal-2cols_left,
.ios .login .modal_header {
  height: 7rem;
}

.ios .event-header_image img {
  top: 2rem;
}

.ios .event-header_title h1 {
  padding: 2rem 2.5rem 0;
}

.ios .login .event-header_title h1 {
  padding: 1rem 2.5rem 0;
}




